<template>
    <div>
        <compass />
        <div class="sideGroup">
            <div class="_group">
                <button type="button" class="_trigger" @click="setScale(-1)"><i class="cubeic-remove"></i></button>
                <button type="button" class="_trigger" @click="setScale(1)"><i class="cubeic-add"></i></button>
            </div>
        </div>

        <div class="view">
            <div class="mapCont" id="mapCont">
                <!-- 路径层 -->
                <div class="layer">
                    <svg width="1000" height="1000" xmlns="http://www.w3.org/2000/svg" v-if="naviLines && naviLines.length">
                        <!-- <polyline v-for="(item,index) in pathData" :key="'showPath'+index" :points="item.map(e=>{return e.x+','+e.y}).join(' ')" 
              class="basePath"/> -->
                        <!-- 导航结果展示 -->
                        <polyline v-for="(item, index) in naviLines" :key="'navline'+index" :points="item.map(e=>{return e.x+','+e.y}).join(' ')" class="resultPath" :class="{is_current: index===naviStepNumber-1}" />
                    </svg>
                </div>
                <!-- 建筑层 -->
                <div class="layer">
                    <svg width="1000" height="1000" xmlns="http://www.w3.org/2000/svg" id="svgElements" @click.self="resetSvg();resetNav();">
                        <template v-for="(item,index) in polygons">
                            <polygon :key="'polygon'+index" :class="{isBlock: item.block, svgArea: true}" :style="item.type" :points="item.points" :data-x="item.attrDataX" :data-y="item.attrDataY" :title="item.attrTitle" @click="clickElement($event, item)" />
                        </template>
                    </svg>
                </div>
                <!-- 漂浮层 -->
                <div class="layer mapFloat">
                    <template v-if="!item.block" v-for="(item,index) in polygons">
                        <span :key="'float'+index" class="_title" :style="{left:item.attrDataX+'px', top:item.attrDataY+'px'}">{{item.attrTitle}}</span>
                    </template>
                </div>
            </div>
        </div>
        <!-- 导航1 -->
        <cube-popup type="nav-step-1" :mask="false" ref="navStep1Box" position="bottom">
            <div class="cube-extend-popup-content">
                <slot>
                    <div class="extend-info" v-if="currentPolygon">
                        <h3>{{currentPolygon.attrTitle}}</h3>
                        <p>{{currentPolygon.desc}}</p>
                    </div>
                    <div class="flex-row" style="border:1px solid #eee">
                        <div class="flex-1">
                            <cube-button icon="cubeic-back" :light="true" @click="naveSections.start = currentPolygon;navStep2()">从这里出发</cube-button>
                        </div>
                        <div class="flex-1">
                            <cube-button icon="cubeic-arrow" :light="true" @click="naveSections.end = currentPolygon;navStep2()">到这里去</cube-button>
                        </div>
                    </div>
                </slot>
            </div>
        </cube-popup>
        <!-- 导航2 -->
        <cube-popup type="nav-step-2" :mask="false" ref="navStep2Box" position="bottom">
            <div class="cube-extend-popup-content">
                <slot>
                    <div class="extend-info">
                        <div class="flex-row align-center m">
                            起点：
                            <div class="flex-1">
                                {{naveSections.start ? naveSections.start.attrTitle : "继续拾取起点"}}
                            </div>
                        </div>
                        <div class="flex-row align-center m">
                            终点：
                            <div class="flex-1">
                                {{naveSections.end ? naveSections.end.attrTitle : "继续拾取终点"}}
                            </div>
                        </div>
                    </div>

                    <cube-button :primary="true" :disabled="!naveSections.start || !naveSections.end" @click="search">开始导航</cube-button>
                </slot>
            </div>
        </cube-popup>
        <!-- 导航3 -->
        <cube-popup type="nav-step-3" :mask="false" ref="navStep3Box" position="bottom">
            <div class="cube-extend-popup-content">
                <slot>
                    <div class="flex-row aimMarker">
                        <div class="flex-1">
                            <p class="el"><i class="cubeic-aim"></i>起点：<span class="_point">{{naveSections.start && naveSections.start.attrTitle}}</span></p>
                        </div>
                        <div class="flex-1">
                            <p class="el"><i class="cubeic-navigation"></i>终点：<span class="_point">{{naveSections.end && naveSections.end.attrTitle}}</span></p>
                        </div>
                    </div>
                    <cube-slide ref="slide" style="margin-bottom:10px" :data="navResult" :autoPlay="false" :loop="false" @change="naviStepNumber=$event+1">
                        <cube-slide-item v-for="(item, index) in navResult" :key="index">
                            <div class="nav-item extend-info">{{item.direction}}{{item.deflection}}{{item.orientation}}{{item.distence}}{{item.shopInfo}}</div>
                        </cube-slide-item>
                    </cube-slide>

                    <cube-button :primary="true" @click="resetNav();">关闭</cube-button>
                </slot>
            </div>
        </cube-popup>

    </div>
</template>

<script>
import instance from "../api/index";
//import * as util from "../assets/util";
import * as core from "../assets/core";

export default {
    components: {
        compass: resolve => require(["@/components/compass.vue"], resolve)
    },
    data() {
        return {
            mainDom: null,
            buildingSVG: null,
            currentPolygon: null,
            polygons: [],
            pathData: [],
            pathResult: null,
            scaleQeueu: [],
            scaleIndex: 2,
            naveSections: {
                start: null,
                end: null
            },
            navResult: [],
            naviStepNumber: 1
        };
    },
    computed: {
        naviLines: function() {
            let result = [];
            if (Array.isArray(this.pathResult) && this.pathResult.length) {
                this.pathResult.forEach((e, i) => {
                    if (i < this.pathResult.length - 1) {
                        let next = this.pathResult[i + 1];
                        result.push([e, next]);
                    }
                });
            }
            return result;
        }
    },
    methods: {
        resetNav: function() {
            this.hidePopInfo();
            this.naviStepNumber = 1;
            this.pathResult = [];
            this.navResult = [];
            this.naveSections.start = null;
            this.naveSections.end = null;
        },
        navStep2: function() {
            this.hidePopInfo();
            this.$nextTick(() => {
                this.$refs.navStep2Box.show();
            });
        },
        hidePopInfo: function() {
            this.$refs.navStep1Box.hide();
            this.$refs.navStep2Box.hide();
            this.$refs.navStep3Box.hide();
        },
        clickElement: function(evt, item) {
            this.resetSvg();
            evt.target.classList.add("active");
            this.currentPolygon = item;
            if (this.naveSections.start) {
                //拾取模式
                this.naveSections.end = item;
                this.$nextTick(() => {
                    this.$refs.navStep2Box.show();
                });
            } else if (this.naveSections.end) {
                this.naveSections.start = item;
                this.$nextTick(() => {
                    this.$refs.navStep2Box.show();
                });
            } else {
                this.$refs.navStep1Box.show();
            }
        },
        resetSvg: function() {
            this.currentPolygon = null;
            if (this.buildingSVG) {
                let actived = this.buildingSVG.querySelectorAll(
                    "polygon.active"
                );
                actived.forEach(polygon => {
                    polygon.classList.remove("active");
                });
            }
        },
        fetchData: function() {
            Promise.all([
                instance.get("map.json"),
                instance.get("path.json")
            ]).then(res => {
                this.polygons = res[0].data;
                this.pathData = res[1].data;
                //test
                // this.naveSections.start = {
                //   connectPoint: {
                //     x:223,
                //     y:126
                //   }
                // }
                // this.naveSections.end = {
                //   connectPoint: {
                //     x: 451,
                //     y: 631
                //   }
                // }
                // this.search();
            });
        },
        search: function() {
            let nav_start = this.naveSections.start.connectPoint;
            let nav_end = this.naveSections.end.connectPoint;
            if (!nav_end || !nav_start || !nav_start.x || !nav_end.x) {
                return console.warn("参数错误");
            }
            //寻路
            core.startSearch(this.pathData, nav_start, nav_end, data => {
                if (Array.isArray(data)) {
                    this.hidePopInfo();
                    core.navigate(data, navi => {
                        console.log(navi);
                        this.navResult = navi.result;
                        this.pathResult = navi.path;
                        this.$refs.navStep3Box.show();
                    });
                } else {
                    this.$createToast({
                        txt: "搜索失败",
                        type: "error",
                        time: 1000
                    }).show();
                    this.resetSvg();
                    this.resetNav();
                }
            });
        },
        setScale: function(step) {
            if (this.mainDom) {
                if (step > 0) {
                    if (this.scaleIndex < this.scaleQeueu.length - 1) {
                        this.scaleIndex++;
                    }
                } else if (step < 0) {
                    if (this.scaleIndex > 0) {
                        this.scaleIndex--;
                    }
                }
                this.mainDom.style.transform =
                    "scale(" + this.scaleQeueu[this.scaleIndex] + ")";
            }
        }
    },
    created: function() {
        this.fetchData();
    },
    mounted: function() {
        this.mainDom = document.getElementById("mapCont");
        this.buildingSVG = document.getElementById("svgElements");
        let clientWidth = window.innerWidth;
        let minScale = clientWidth / 1000;
        let partWidth = parseInt((1000 - clientWidth) / 4);
        let scaleQeueu = [
            minScale,
            (clientWidth + partWidth) / 1000,
            (clientWidth + partWidth * 2) / 1000,
            (clientWidth + partWidth * 3) / 1000,
            1
        ];
        this.scaleQeueu = scaleQeueu;
        this.scaleIndex = 2;
        this.setScale(0);
    }
};
</script>

<style scoped>
.sideGroup {
    position: fixed;
    right: 0.5em;
    top: 0.5em;
    z-index: 9;
}
.sideGroup ._group {
    background: #fff;
    box-shadow: 0 0 2px #ccc;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 1em;
}
.sideGroup ._trigger {
    display: block;
    width: 2em;
    height: 2em;
    line-height: 2em;
    text-align: center;
    font-size: 1.5em;
    border: 0;
    background: #fff;
}
.sideGroup ._trigger.active {
    background: #ccc;
}

.view {
    overflow: scroll;
}

.mapCont {
    width: 1000px;
    height: 1000px;
    transform: scale(0.5);
    transform-origin: left top;
}
.layer {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100;
}

.nav-item {
    position: relative;
    color: #666;
    margin: 2em 1em;
}
.nav-item::before {
    content: "";
    position: absolute;
    left: -1em;
    top: 50%;
    width: 0;
    height: 0;
    overflow: hidden;
    border: 6px solid;
    border-color: transparent transparent transparent #666;
    margin-top: -6px;
}
.aimMarker {color:#999;padding: .5em .5em 1em; border-bottom: 1px solid #eee;}
.aimMarker ._point{color:#666;}
</style>
